.gallery__section {
  background: linear-gradient(#fff5f6 20%, #fff 20%);
  padding-bottom: 80px;
}

.gallery {
  grid-gap: 2px;
  grid: repeat(12, 8px) / repeat(28, 8px);
  justify-content: center;
  display: grid;
}

.gallery__item {
  transition: border-radius box-shadow var(--animation);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10%;
  display: block;
  overflow: hidden;
}

.gallery__item:nth-child(1) {
  grid-area: 5 / 1 / span 4 / span 4;
}

.gallery__item:nth-child(2) {
  grid-area: 4 / 5 / span 4 / span 4;
}

.gallery__item:nth-child(3) {
  grid-area: 8 / 5 / span 4 / span 4;
}

.gallery__item:nth-child(4) {
  border-radius: 5%;
  grid-area: 1 / 9 / span 8 / span 8;
}

.gallery__item:nth-child(5) {
  grid-area: 9 / 9 / span 4 / span 4;
}

.gallery__item:nth-child(6) {
  grid-area: 4 / 17 / span 4 / span 4;
}

.gallery__item:nth-child(7) {
  grid-area: 8 / 17 / span 4 / span 4;
}

.gallery__item:nth-child(8) {
  grid-area: 3 / 21 / span 4 / span 4;
}

.gallery__item:nth-child(9) {
  grid-area: 7 / 21 / span 4 / span 4;
}

.gallery__item:nth-child(10) {
  grid-area: 5 / 25 / span 4 / span 4;
}

.gallery__item:hover, .gallery__item:focus {
  border-radius: 50%;
  box-shadow: 0 2px 10px rgba(212, 20, 67, .3);
}

.gallery__image {
  transition: scale var(--animation);
}

.gallery__item:hover .gallery__image {
  transform: scale(120%);
}

.header {
