  outline: 1px solid #337ab7;
  padding-top: 8px;
  padding-bottom: 8px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .header {
    width: 100vw;
    z-index: 5;
    background-color: rgba(255, 184, 202, .4);
    position: fixed;
    top: 0;
    left: 0;
  }
}

.logo {
  fill: var(--pink-text-color);
  display: block;
}

.header__container {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.menu {
  height: 100vh;
  height: 100%;
  width: 100vw;
  z-index: 5;
  background-color: #e17992;
  padding-top: 60px;
  transition: transform .25s;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
  transform: translateX(100%);
}

.menu._active {
  transform: translateX(0);
}

body._lock {
  overflow: hidden;
}

.menu__icon {
  width: 20px;
  height: 14px;
  cursor: pointer;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: relative;
  top: 0;
  right: 20px;
}

.menu__icon._active {
  width: 14px;
  height: 14px;
  right: 37px;
}

.line {
  height: 10%;
  width: 100%;
  background-color: var(--pink-text-color);
  transition: transform .3s, rotate .3s, scale .2s;
  position: absolute;
  left: 0;
}

.line-1 {
  top: 0;
}

.line-2 {
  top: 50%;
  transform: scale(1)translate(0, -50%);
}

.line-3 {
  bottom: 0;
  right: 0;
}

.menu__icon._active .line {
  background-color: var(--white-text-color);
}

.menu__icon._active .line-1 {
  top: 45%;
